<template>
    <!-- Login box start -->
		<div class="login-page">
			<form class = "pt-5" @submit.prevent="signin">
			<div class="login-box mt-5">
				<div class="login-form">
					<router-link to="#" class="login-logo">
						<img src="./assets/images/castro.jpeg" alt="Vico Admin" />
					</router-link>
					<div class="login-welcome mb-1">
						Bem vindo ao sistema de gestão integrada do IPPC, <br /> Por favor insira as suas credencias para aceder a sua conta.
					</div>
					<div class="mb-3">
						<label class="form-label "><span class = "fas fa-user"></span> Utilizador</label>
						<input type="text" class="form-control" v-model = "auth.email">
					</div>
					<div class="mb-3">
						<label class="form-label"><span class = "fas fa-key"></span> Palavra-Passe</label>
						<input type="password" class="form-control" v-model = "auth.senha">
					</div>
				</div>
				<button type="submit" class = "btn btn-warning shadow-sm w-100 border-0 rounded-3 text-uppercase">Iniciar Sessão <span class = "fas fa-sign-in-alt"></span></button>
			</div>
		</form>

		<h3 style="font-size: 20px; margin-top: 40px; text-align: center; color:grey">© Instituto Politécnico Privado Castro "A Luz Do Saber", {{ new Date().getFullYear() }} <br/> Todos os Direitos Reservados</h3>
		</div>
		<!-- Login box end -->
</template>
<script>
    import { api, url } from '@/helpers/api';
	import './static'
	import { mapMutations } from 'vuex'
	import axios from 'axios';

    export default{
		name:"login_view",
		data(){
			return{
				auth:{
					email:'',
					senha:'',
				}
			}
		},
		mounted(){
			this.checkLicence()
		},
		methods:{
			...mapMutations(['login']),
			signin(e){
				e.preventDefault()
				if(this.auth.email === "" || this.auth.senha === ""){
					this.$swal.fire({
						title:"Alerta",
						icon:"warning",
						text:"Preencha Todos os campos"
					})
				}else{
					axios
					.post(`${url}/api/utilizadores/login`, this.auth)
					.then(res => {
						if(res.data.token){
							localStorage.setItem("usuario", JSON.stringify(res.data))
							localStorage.setItem("user", JSON.stringify(res.data.data))
							this.login({user:res.data.data,token:res.data.token});
							this.$router.push("/dashboard")
						}else{
							this.$swal.fire({
								title:"Erro",
								text:"Utilizador ou Palavra-Passe Incorrectos",
								icon:"warning"
							})
						}
					})
					.catch(() => {
						this.$swal.fire({
							title:"Erro",
							text:"Utilizador ou Palavra-Passe Incorrectos",
							icon:"warning"
						})
					})
				}
			},
			checkLicence(){
				api
				.get("/license/read/")
				.then(res => {
					console.log(res.data)
				})
				.catch(err => {
					if(err.response.data.message === "Licença não encontrada"){
						this.$router.push("/licence")
					}else if (err.response.data.message === "Licença Expirada"){
						this.$router.push("/licence")
					}
				})
			}
		}
    }

</script>
<style scoped>
.login-page {
	background:rgb(63, 62, 62);
	position:fixed;
	top:0;
	left:0;
	right:0;
	bottom:0;
	padding-top:80px;
}
.form-label {
	color:black;
}
</style>